export default {
	props: {
		value: [String, Number],
		type: {
			type: String,
			default: 'text'
		},
		password: Boolean,
		placeholder: String,
		clearable: Boolean,
		placeholderStyle: String,
		placeholderClass: String,
		disabled: Boolean,
		focus: Boolean,
		maxlength: {
			type: [Number, String],
			default: 140
		},
		cursorSpacing: {
			type: Number,
			default: 0
		},
		confirmType: {
			type: String,
			default: 'done'
		},
		confirmHold: Boolean,
		adjustPosition: {
			type: Boolean,
			default: true
		},
		holdKeyboard: {
			type: Boolean,
			default: false
		},
		prefixIcon: String,
		suffixIcon: String
	},

	data() {
		return {
			value2: null,
			isFocus: false
		};
	},

	watch: {
		value: {
			immediate: true,
			handler(val) {
				this.value2 = val;
			}
		}
	},

	methods: {
		onInput() {
			this.$emit('input', this.value2);
			this.$emit('change', this.value2);
		},

		onFocus(e) {
			this.$emit('focus', e);
			this.isFocus = true;
		},

		onBlur(e) {
			this.$emit('blur', e);

			setTimeout(() => {
				this.isFocus = false;
			}, 0);
		},

		onConfirm(e) {
			this.$emit('confirm', e);
			this.search();
		},

		onKeyboardheightchange(e) {
			this.$emit('keyboardheightchange', e);
		},

		search() {
			this.$emit('search', this.value2);
		},

		clear() {
			this.$emit('input', '');
			this.$emit('change', '');
		}
	}
};
